import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { useNotification } from '../contexts/NotificationContext'
import '../styles/forms.css'

const UldForm = () => {
  const [formData, setFormData] = useState({
    uldCode: '',
    uldType: '',
    maxWeight: '',
    tare: '',
    allowable: '',
    currentWeight: '',
    volume: '',
    capacityUsage: '',
    remainingWeight: ''
  })

  const [errors, setErrors] = useState({})
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState('')
  const [messageType, setMessageType] = useState('')
  
  const { showSuccess, showError, showInfo } = useNotification()
  const [isMobile, setIsMobile] = useState(false)
  const [capacityWarning, setCapacityWarning] = useState(null)
  const [isTareEditable, setIsTareEditable] = useState(false)
  const [selectedAircraft, setSelectedAircraft] = useState('')
  const [selectedAirline, setSelectedAirline] = useState('')

  const API_BASE_URL = 'http://localhost:3001'

  // Override kurallar - Uak/airline zel kstlamalar
  const overrideRules = {
    aircraft: {
      'B767': {
        'AKH': { mgw: 1135, tare: 88 }, // AKH'yi 1.135 kg'a sabitleyen filo
        'DPE': { mgw: 1134, tare: 83 }  // DPE iin zel limit
      },
      'B777': {
        'AKH': { mgw: 1588, tare: 88 }  // B777'de AKH AKE ile ayn limit
      }
    },
    airline: {
      'TK': {
        'AKH': { mgw: 1135, tare: 88 }, // Turkish Airlines zel kstlamas
        'PMC': { mgw: 6804, tare: 102 } // TK palet limiti
      },
      'LH': {
        'AKH': { mgw: 1588, tare: 88 }, // Lufthansa AKH'yi AKE ile eit tutar
        'PMC': { mgw: 6800, tare: 102 }
      }
    }
  }

  // Override kurallarn uygulayan fonksiyon
  const getUldMetricsWithOverride = (uldType) => {
    const baseUld = uldTypes.find(uld => uld.value === uldType)
    if (!baseUld) return null

    let mgw = baseUld.mgw
    let tare = baseUld.tare

    // Uak override' kontrol et
    if (selectedAircraft && overrideRules.aircraft[selectedAircraft]?.[uldType]) {
      const aircraftOverride = overrideRules.aircraft[selectedAircraft][uldType]
      mgw = aircraftOverride.mgw
      tare = aircraftOverride.tare
    }

    // Airline override' kontrol et (uak override'ndan sonra)
    if (selectedAirline && overrideRules.airline[selectedAirline]?.[uldType]) {
      const airlineOverride = overrideRules.airline[selectedAirline][uldType]
      mgw = airlineOverride.mgw
      tare = airlineOverride.tare
    }

    return {
      ...baseUld,
      mgw,
      tare,
      volume: baseUld.volume,
      type: baseUld.type
    }
  }

  // Mobile detection
  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth <= 768)
    }
    
    checkMobile()
    window.addEventListener('resize', checkMobile)
    
    return () => window.removeEventListener('resize', checkMobile)
  }, [])

  // Uak veya airline deitiinde mevcut ULD tipi iin override' yeniden uygula
  useEffect(() => {
    if (formData.uldType) {
      const selectedUld = getUldMetricsWithOverride(formData.uldType)
      if (selectedUld) {
        const mgw = selectedUld.mgw
        const tare = selectedUld.tare
        const volume = selectedUld.volume
        const isPallet = selectedUld.type === 'pallet'
        const allowable = Math.max(0, mgw - tare)
        
        setFormData(prev => ({
          ...prev,
          maxWeight: mgw.toString(),
          tare: tare.toString(),
          volume: isPallet ? '' : (volume ? volume.toString() : ''),
          allowable: allowable.toString(),
          capacityUsage: '',
          remainingWeight: ''
        }))
      }
    }
  }, [selectedAircraft, selectedAirline])

  // ULD tipleri - Sistemde sabit hesaplama kurallarna gre gncellendi
  const uldTypes = [
    { value: 'AKE', label: 'AKE - LD3 Container', name: 'LD3', mgw: 1588, tare: 82, volume: 4.4, type: 'container', description: 'Taban 156,2153,4 cm, ykseklik ~162,5 cm' },
    { value: 'AKH', label: 'AKH - LD3 (alak) Container', name: 'LD3', mgw: 1135, tare: 88, volume: 3.6, type: 'container', description: 'AKE ile ayn taban, ykseklik ~113 cm' },
    { value: 'DPE', label: 'DPE - LD2 Container', name: 'LD2', mgw: 1179, tare: 83, volume: 3.45, type: 'container', description: '767 ailesinde yaygn; AKE\'den daha dar' },
    { value: 'DPW', label: 'DPW - LD3 Container (varyant)', name: 'LD3', mgw: 1588, tare: 80, volume: 4.4, type: 'container', description: 'LD-3 ailesiyle ayn geometri' },
    { value: 'AMJ', label: 'AMJ - LD9 (10-ft) Container', name: 'LD9', mgw: 6804, tare: 340, volume: 9.5, type: 'container', description: 'Taban 125"96" (317,5243,8 cm), ykseklik 96"' },
    { value: 'AMA', label: 'AMA - LD9 / GM1 Container', name: 'LD9', mgw: 6804, tare: 410, volume: 9.5, type: 'container', description: 'AMJ ile ayn boyut snf' },
    { value: 'AAF', label: 'AAF - LD26 Container', name: 'LD26', mgw: 5329, tare: 271, volume: 13.6, type: 'container', description: 'Taban 125"88" (317,5223,5 cm), ykseklik ~162,6 cm' },
    { value: 'AMF', label: 'AMF - LD11 Container', name: 'LD11', mgw: 3175, tare: 192, volume: 7.3, type: 'container', description: 'Taban 125"88", ykseklik ~162,6 cm' },
    { value: 'PMC', label: 'PMC - 96125 Palet (P6P)', name: 'LD7', mgw: 6800, tare: 102, volume: null, type: 'pallet', description: 'Ana gvertede MGW genelde 6.800 kg' },
    { value: 'PLA', label: 'PLA - 60,4125 Palet', name: 'LD6', mgw: 3175, tare: 93, volume: null, type: 'pallet', description: 'Dar tabanl palet' },
    { value: 'PAG', label: 'PAG - 88125 Palet', name: '88x125', mgw: 5715, tare: 96, volume: null, type: 'pallet', description: 'Ana gvertede ~6.000-6.804 kg, alt gvertede ~4.626 kg' },
    { value: 'PAJ', label: 'PAJ - 96125 Palet', name: '96x125', mgw: 6800, tare: 102, volume: null, type: 'pallet', description: 'PMC ile ayn snf' }
  ];

  // ULD tipi deitiinde otomatik hesaplama - Override sistemi ile
  const handleUldTypeChange = (e) => {
    const selectedType = e.target.value
    const selectedUld = getUldMetricsWithOverride(selectedType)
    
    if (selectedUld) {
      const mgw = selectedUld.mgw
      const tare = selectedUld.tare
      const volume = selectedUld.volume
      const isPallet = selectedUld.type === 'pallet'
      
      // HESAP KURALLARI (Override sistemi ile):
      // Yklenebilir Arlk (kg) = MGW - Tare
      const allowable = Math.max(0, mgw - tare)
      
      setFormData(prev => ({
        ...prev,
        uldType: selectedType,
        maxWeight: mgw.toString(),
        tare: tare.toString(),
        volume: isPallet ? '' : (volume ? volume.toString() : ''),
        allowable: allowable.toString(),
        capacityUsage: '',
        remainingWeight: ''
      }))
       
       // Tare dzenleme modu sfrlanr
       setIsTareEditable(false)
       
       // ULD seildiinde otomatik odak kaldrld - ekran frlamasn nlemek iin
      
      // Tare > MGW uyars
      if (tare > mgw) {
        setCapacityWarning({
          type: 'warning',
          message: 'Tare, MGW\'den byk  Yklenebilir arlk 0\'a drld.'
        })
      } else {
        setCapacityWarning(null)
      }
    } else {
      setFormData(prev => ({
        ...prev,
        uldType: selectedType,
        maxWeight: '',
        tare: '',
        volume: '',
        allowable: '',
        capacityUsage: '',
        remainingWeight: ''
      }))
      setCapacityWarning(null)
    }
  }

  // Input deiim handler'
  const handleInputChange = (e) => {
    const { name, value } = e.target
    
    if (name === 'currentWeight') {
      handleCurrentWeightChange(e)
    } else if (name === 'uldCode') {
      handleUldCodeChange(e)
    } else if (name === 'tare') {
      handleTareChange(e)
    } else {
      setFormData(prev => ({ ...prev, [name]: value }))
      
      if (errors[name]) {
        setErrors(prev => ({
          ...prev,
          [name]: ''
        }))
      }
    }
  }

  // ULD kodu validasyonu
  const handleUldCodeChange = (e) => {
    const { value } = e.target
    setFormData(prev => ({ ...prev, uldCode: value.toUpperCase() }))
    
    if (errors.uldCode) {
      setErrors(prev => ({
        ...prev,
        uldCode: ''
      }))
    }
    
    if (value && !/^[A-Z]{3}[0-9]{5}[A-Z]{2}$/.test(value.toUpperCase())) {
      setErrors(prev => ({
        ...prev,
        uldCode: 'ULD Kodu format hatal. rn: AKE12345AB'
      }))
    }
  }

  // Tare dzenleme modunu a/kapat
  const toggleTareEdit = () => {
    setIsTareEditable(!isTareEditable)
    
    // Dzenleme modu kapatlrsa varsaylan deere geri dn
    if (isTareEditable) {
      const selectedUld = uldTypes.find(uld => uld.value === formData.uldType)
      if (selectedUld) {
        const defaultTare = selectedUld.tare
        setFormData(prev => ({ ...prev, tare: defaultTare.toString() }))
        
        // Yklenebilir arlk yeniden hesaplanr
        const mgw = parseFloat(formData.maxWeight)
        const allowable = Math.max(0, mgw - defaultTare)
        setFormData(prev => ({
          ...prev,
          allowable: allowable.toString(),
          capacityUsage: '',
          remainingWeight: ''
        }))
        
        // Mevcut arlk varsa kapasite yeniden hesaplanr
        if (formData.currentWeight) {
          handleCurrentWeightChange({ target: { name: 'currentWeight', value: formData.currentWeight } })
        }
      }
    }
  }

  // Tare deiikliklerini kaydet
  const saveTareChanges = () => {
    setIsTareEditable(false)
    // Deiiklikler zaten formData'da, sadece modu kapat
  }

  // Tare deiikliklerini iptal et
  const cancelTareChanges = () => {
    const selectedUld = uldTypes.find(uld => uld.value === formData.uldType)
    if (selectedUld) {
      const defaultTare = selectedUld.tare
      setFormData(prev => ({ ...prev, tare: defaultTare.toString() }))
      
      // Yklenebilir arlk yeniden hesaplanr
      const mgw = parseFloat(formData.maxWeight)
      const allowable = Math.max(0, mgw - defaultTare)
      setFormData(prev => ({
        ...prev,
        allowable: allowable.toString(),
        capacityUsage: '',
        remainingWeight: ''
      }))
      
      // Mevcut arlk varsa kapasite yeniden hesaplanr
      if (formData.currentWeight) {
        handleCurrentWeightChange({ target: { name: 'currentWeight', value: formData.currentWeight } })
      }
    }
    setIsTareEditable(false)
  }

  // Tare deitiinde otomatik hesaplama
  const handleTareChange = (e) => {
    const { value } = e.target
    setFormData(prev => ({ ...prev, tare: value }))
    
    if (errors.tare) {
      setErrors(prev => ({
        ...prev,
        tare: ''
      }))
    }
    
    // Tare deitiinde yklenebilir arlk ve kapasite yeniden hesaplanr
    if (value && formData.maxWeight) {
      const tare = parseFloat(value)
      const mgw = parseFloat(formData.maxWeight)
      
      if (tare < 0) {
        setErrors(prev => ({
          ...prev,
          tare: 'Tare 0\'dan kk olamaz'
        }))
        return
      }
      
      if (tare > mgw) {
        setErrors(prev => ({
          ...prev,
          tare: 'Tare, MGW\'den byk olamaz'
        }))
        return
      }
      
      // HESAP KURALLARI (Sistemde sabit):
      // Yklenebilir Arlk (kg) = MGW - Tare
      const allowable = Math.max(0, mgw - tare)
      
      setFormData(prev => ({
        ...prev,
        allowable: allowable.toString(),
        capacityUsage: '',
        remainingWeight: ''
      }))
      
      // Mevcut arlk varsa kapasite yeniden hesaplanr
      if (formData.currentWeight) {
        handleCurrentWeightChange({ target: { name: 'currentWeight', value: formData.currentWeight } })
      }
    }
  }

  // Mevcut arlk deitiinde hesaplama - Sistemde sabit kurallara gre
  const handleCurrentWeightChange = (e) => {
    const { value } = e.target
    setFormData(prev => ({ ...prev, currentWeight: value }))
    
    if (errors.currentWeight) {
      setErrors(prev => ({
        ...prev,
        currentWeight: ''
      }))
    }
    
    if (value && formData.allowable) {
      const current = parseFloat(value)
      const allowable = parseFloat(formData.allowable)
      
             if (current <= 0) {
         setErrors(prev => ({
           ...prev,
           currentWeight: 'Sfrdan byk bir deer giriniz'
         }))
         return
       }
      
      if (current > allowable) {
        setErrors(prev => ({
          ...prev,
          currentWeight: 'Arlk yklenebilir limiti ayor'
        }))
      }
      
      // HESAP KURALLARI (Sistemde sabit):
      // Kapasite % = MevcutArlk / YklenebilirArlk  100
      const usage = allowable > 0 ? ((current / allowable) * 100).toFixed(1) : '0.0'
      
      // Kalan Arlk (kg) = YklenebilirArlk - MevcutArlk
      const remaining = Math.max(0, allowable - current).toFixed(1)
      
      setFormData(prev => ({
        ...prev,
        capacityUsage: usage,
        remainingWeight: remaining
      }))
      
      // Kapasite uyarlar
      updateCapacityWarning()
    } else {
      setFormData(prev => ({
        ...prev,
        capacityUsage: '',
        remainingWeight: ''
      }))
      setCapacityWarning(null)
    }
  }

  // Kapasite uyarsn gncelle - Yeni kurallara gre
  const updateCapacityWarning = () => {
    if (!formData.allowable || !formData.currentWeight) {
      setCapacityWarning(null);
      return;
    }

    const capacityPercent = parseFloat(formData.capacityUsage);
    
    if (capacityPercent > 100.0) {
      setCapacityWarning({ 
        type: 'danger', 
        message: ` ULD kapasitesi ald! Yk kabul edilmez! (${capacityPercent.toFixed(1)}%)` 
      });
    } else if (capacityPercent === 100.0) {
      setCapacityWarning({ 
        type: 'critical', 
        message: ' Kapasite %100 doldu! Yeni yk eklenirse am olur.' 
      });
    } else if (capacityPercent >= 90.0) {
      setCapacityWarning({ 
        type: 'warning', 
        message: ` Kritik seviye! Kapasite %${capacityPercent.toFixed(1)} - Dikkat!` 
      });
    } else if (capacityPercent >= 70.0) {
      setCapacityWarning({ 
        type: 'caution', 
        message: ` Dikkat seviyesi! Kapasite %${capacityPercent.toFixed(1)}` 
      });
    } else {
      setCapacityWarning(null); // %0-69 aras uyar yok (yeil)
    }
  };

  // Submit butonunun disabled durumu
  const isSubmitDisabled = () => {
    return (
      !formData.uldCode ||
      !formData.uldType ||
      !formData.currentWeight ||
      parseFloat(formData.currentWeight) < 0 ||
      parseFloat(formData.currentWeight) > parseFloat(formData.allowable || 0) // Amda devre d
    );
  };

  // Submit buton tooltip mesaj
  const getSubmitButtonTooltip = () => {
    if (!formData.uldCode) return 'Zorunlu alanlar doldurun'
    if (!formData.uldType) return 'Zorunlu alanlar doldurun'
    if (!formData.currentWeight) return 'Zorunlu alanlar doldurun'
    if (Object.keys(errors).length > 0) return 'Validasyon hatalarn dzeltin'
    if (parseFloat(formData.currentWeight) > parseFloat(formData.allowable || 0)) return 'Arlk limiti ayor  kaydetme kapal'
    return 'Formu kaydetmek iin tklayn'
  }

  // Form validasyonu
  const validateForm = () => {
    const newErrors = {}
    
    // ULD Kodu validasyonu
    if (!formData.uldCode) {
      newErrors.uldCode = 'ULD kodu zorunludur'
    } else if (!/^[A-Z]{3}[0-9]{5}[A-Z]{2}$/.test(formData.uldCode)) {
      newErrors.uldCode = 'ULD Kodu format hatal. rn: AKE12345AB'
    }
    
    // ULD Tipi validasyonu
    if (!formData.uldType) {
      newErrors.uldType = 'ULD Tipi seilmelidir'
    }
    
    // Tare validasyonu
    if (formData.tare) {
      const tare = parseFloat(formData.tare)
      const mgw = parseFloat(formData.maxWeight)
      
      if (tare < 0) {
        newErrors.tare = 'Tare 0\'dan kk olamaz'
      } else if (tare > mgw) {
        newErrors.tare = 'Tare, MGW\'den byk olamaz'
      }
    }
    
     // Mevcut Arlk validasyonu - enabled ise zorunlu
     if (formData.uldType) { // ULD seilmise enabled
       if (formData.currentWeight) {
         if (parseFloat(formData.currentWeight) < 0) {
           newErrors.currentWeight = 'Sfrdan byk bir deer giriniz'
         } else if (formData.allowable && parseFloat(formData.currentWeight) > parseFloat(formData.allowable)) {
           newErrors.currentWeight = 'Arlk yklenebilir limiti ayor'
         }
       } else {
         newErrors.currentWeight = 'Mevcut arlk zorunludur'
       }
     }
     // ULD seilmemise (disabled) hi validasyon yapmaz
    
    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  // Form submit
  const handleSubmit = async (e) => {
    e.preventDefault()
    
    if (!validateForm()) {
      setMessage('Ltfen zorunlu alanlar doldurun ve validasyon hatalarn dzeltin')
      setMessageType('error')
      return
    }

                   if (parseFloat(formData.currentWeight) > parseFloat(formData.allowable || 0)) {
        setMessage(' Arlk limiti ald iin kayt yaplamaz')
        setMessageType('error')
        return
      }
    
    setLoading(true)
    setMessage('')

    try {
      const response = await axios.post(`${API_BASE_URL}/api/uld`, formData)
      
      if (response.status === 201) {
        showSuccess('ULD baaryla kaydedildi!', 'Kayt Baarl')
        
        setFormData({
          uldCode: '',
          uldType: '',
          maxWeight: '',
          tare: '',
          allowable: '',
          currentWeight: '',
          volume: '',
          capacityUsage: '',
          remainingWeight: ''
        })
        setErrors({})
        setMessage('')
        setCapacityWarning(null)
      }
    } catch (error) {
      console.error('ULD form kaydetme hatas:', error)
      setMessage('ULD form kaydedilirken hata olutu')
      setMessageType('error')
      showError('ULD kaydedilirken hata olutu. Ltfen tekrar deneyin.', 'Kayt Hatas')
    } finally {
      setLoading(false)
    }
  }

  // Form temizleme
  const handleClearForm = () => {
    setFormData({
      uldCode: '',
      uldType: '',
      maxWeight: '',
      tare: '',
      allowable: '',
      currentWeight: '',
      volume: '',
      capacityUsage: '',
      remainingWeight: ''
    })
    setErrors({})
    setMessage('')
    setMessageType('')
    setCapacityWarning(null)
    setIsTareEditable(false)
    showInfo('Form sfrland.', 'lem Tamamland')
  }

  // Kapasite bar yzdesini hesapla (bar asla 100%' gemez)
  const getCapacityBarPercentage = () => {
    const usage = parseFloat(formData.capacityUsage) || 0;
    return Math.min(Math.max(0, usage), 100); // min(usage%, 100)
  };

  // getProgressWarningText fonksiyonu kaldrld - artk kullanlmyor

  // Kapasite bar rengini belirle - yiletirilmi eikler
  const getCapacityBarColor = () => {
    const usage = parseFloat(formData.capacityUsage) || 0;
    if (usage >= 100.0) {
      return '#DC2626'; // Krmz (100% - Limit am, kabul edilmez)
    } else if (usage >= 95.0) {
      return '#EA580C'; // Turuncu (95-99.9% - Kritik seviye, dikkat)
    } else if (usage >= 70.0) {
      return '#F59E0B'; // Sar (70-94.9% - Uyar)
    } else {
      return '#16A34A'; // Yeil (0-69.9% - Normal)
    }
  };

  // Helper text fonksiyonlar
  const getUldCodeHelper = () => {
    if (!formData.uldCode) {
      return { text: 'ULD kodu giriniz (rn: AKE12345AB)', type: 'info' };
    }
    
    if (errors.uldCode) {
      return { text: errors.uldCode, type: 'error' };
    }
    
    return { text: 'Format: XXX99999XX (3 harf + 5 rakam + 2 harf)', type: 'info' };
  };

  const getUldTypeHelper = () => {
    if (!formData.uldType) {
      return { text: 'ULD tipi seiniz', type: 'info' };
    }
    
    return { text: 'ULD tipi seildi', type: 'success' };
  };

  const getMaxWeightHelper = () => {
    if (!formData.uldType) {
      return { text: 'ULD tipine gre otomatik hesaplanr', type: 'info' };
    }
    
    return { text: 'ULD tipine gre otomatik', type: 'info' };
  };

  const getTareHelper = () => {
    if (!formData.uldType) {
      return { text: 'ULD tipine gre otomatik hesaplanr', type: 'info' };
    }
    
    if (isTareEditable) {
      const selectedUld = uldTypes.find(uld => uld.value === formData.uldType);
      const defaultTare = selectedUld?.tare;
      return { text: `Varsaylan: ${defaultTare} kg  Manuel dzenleniyor`, type: 'info' };
    }
    
    if (formData.tare) {
      const selectedUld = uldTypes.find(uld => uld.value === formData.uldType);
      const defaultTare = selectedUld?.tare;
      const currentTare = parseFloat(formData.tare);
      
      if (defaultTare && Math.abs(currentTare - defaultTare) > 0.1) {
        return { text: `Manuel dzenlendi: ${formatKg(currentTare)} kg`, type: 'warning' };
      }
      return { text: 'ULD tipine gre otomatik', type: 'info' };
    }
    
    return { text: 'ULD tipine gre otomatik', type: 'info' };
  };

  const getAllowableHelper = () => {
    if (!formData.uldType) {
      return { text: 'ULD tipine gre otomatik hesaplanr', type: 'info' };
    }
    
    return { text: 'MGW  Tare', type: 'info' };
  };

  const getCurrentWeightHelper = () => {
    // ULD Tipi seilmemise - tek koul
    if (!formData.uldType) {
      return {
        type: 'disabled',
        text: 'nce ULD Tipi seiniz'
      };
    }
    
    // MGW ve Tare saysal + NaN guard
    const mgw = Number(formData.maxWeight) || 0;
    const tare = Number(formData.tare) || 0;
    const current = Number(formData.currentWeight) || 0;
    
    // usable = Math.max((Number(mgw) - Number(tare)), 0)
    const usable = Math.max((mgw - tare), 0);
    
    // Hesaplanamadysa helper uyars (input yine enabled)
    if (usable <= 0 || isNaN(usable)) {
      return {
        type: 'error',
        text: 'Hesaplanamad  ULD bilgilerini kontrol edin'
      };
    }
    
    // Mevcut arlk girilmemise
    if (!formData.currentWeight || current === 0) {
      return {
        type: 'normal',
        text: `Geerli aralk: 0${formatKg(usable)} kg`
      };
    }
    
    // Negatif deer kontrol
    if (current < 0) {
      return {
        type: 'error',
        text: 'Sfrdan byk bir deer giriniz'
      };
    }
    
    const usage = (current / usable) * 100;
    const remaining = usable - current;
    
    // Limit am kontrol (kapasite am)
    if (current > usable) {
      return {
        type: 'error',
        text: ' Limit ald'
      };
    }
    
    // %100 doluluk
    if (usage >= 100) {
      return {
        type: 'error',
        text: ' Limit ald'
      };
    }
    
    // %95-100 aras (kritik seviye)
    if (usage >= 95) {
      return {
        type: 'critical',
        text: ` Kritik seviye! Kapasite %${usage.toFixed(0)} doldu`
      };
    }
    
    // %70-95 aras (uyar)
    if (usage >= 70) {
      return {
        type: 'warning',
        text: `Kapasite yaklayor (%${usage.toFixed(0)})`
      };
    }
    
    // Normal durum ( %70)
    return {
      type: 'normal',
      text: `Geerli aralk: 0${formatKg(usable)} kg  Kalan: ${formatKg(remaining)} kg`
    };
  };

  // getCapacityUsageHelper fonksiyonu kaldrld - artk kullanlmyor

  const getRemainingWeightHelper = () => {
    // ULD Tipi seilmemise sadece bilgi metni
    if (!formData.uldType) {
      return { text: 'ULD tipi seildikten sonra hesaplanr', type: 'info' };
    }
    
    // Mevcut arlk girilmemise bilgi metni
    if (!formData.currentWeight) {
      return { text: 'Mevcut arlk girildikten sonra hesaplanr', type: 'info' };
    }
    
    const remaining = parseFloat(formData.remainingWeight) || 0;
    
    if (remaining < 0) {
      return { text: ` Kapasite ${formatKg(Math.abs(remaining))} kg ald`, type: 'error' };
    }
    
    return { text: 'Kalan yklenebilir arlk', type: 'info' };
  };

  const getVolumeHelper = () => {
    if (!formData.uldType) {
      return { text: 'ULD tipine gre otomatik hesaplanr', type: 'info' };
    }
    
    const selectedUld = uldTypes.find(uld => uld.value === formData.uldType);
    const isPallet = selectedUld?.type === 'pallet';
    
    if (isPallet) {
      if (formData.volume) {
        return { text: `Girilen hacim: ${parseFloat(formData.volume).toFixed(1)} m (yk konturne gre)`, type: 'info' };
      }
      return { text: 'Opsiyonel: Yk konturne/rtsne gre manuel girebilirsiniz. Dimensional Weight hesaplamas iin cm lleri kullann.', type: 'info' };
    } else {
      if (formData.volume) {
        return { text: `Hacim: ${parseFloat(formData.volume).toFixed(1)} m`, type: 'info' };
      }
      return { text: 'ULD tipine gre otomatik hesaplanr', type: 'info' };
    }
  };

  // getUsagePercentage fonksiyonu kaldrld - artk kullanlmyor

  // Yeni say formatlamas - kg binlik ayral, % 1 ondalk
  const formatNumber = (value, decimals = 1) => {
    if (!value || isNaN(value)) return '';
    const num = parseFloat(value);
    if (Number.isInteger(num)) {
      return num.toLocaleString('tr-TR'); // Binlik ayra ile
    }
    return num.toFixed(decimals);
  };

  // kg deerleri iin binlik ayral formatla
  const formatKg = (value) => {
    if (!value || isNaN(value)) return '';
    const num = parseFloat(value);
    return num.toLocaleString('tr-TR'); // Binlik ayra: 6.557
  };

  // Yzde deerleri iin 1 ondalk
  const formatPercent = (value) => {
    if (!value || isNaN(value)) return '';
    const num = parseFloat(value);
    return num.toFixed(1); // 1 ondalk: 43.6
  };

  // Am bilgilerini hesapla
  const getOverflowInfo = () => {
    const usage = parseFloat(formData.capacityUsage) || 0;
    const current = parseFloat(formData.currentWeight) || 0;
    const allowable = parseFloat(formData.allowable) || 0;
    
    if (usage <= 100 || allowable <= 0) return null;
    
    const overKg = current - allowable;
    const overPercent = usage - 100;
    
    return {
      kg: overKg,
      percent: overPercent
    };
  };

  const mobileClass = isMobile ? 'mobile' : ''

  return (
    <div className="uld-page awb-form-container">
      <div className="form-container">
        {/* Professional Form Header */}
                 <div className="form-header">
           <h1 className="form-title">
             ULD Kapasite Ynetimi
           </h1>
           <p className="form-subtitle">ULD (Unit Load Device) kapasite ve hacim hesaplamalar</p>
                       
         </div>
      
      {/* Form Mesaj */}
      {message && (
        <div className={`form-message ${messageType} ${mobileClass}`}>
          {message}
        </div>
      )}
      
      <div className="form-body">
        <form onSubmit={handleSubmit} className="dg-form" autoComplete="off">
          {/* ULD Bilgileri */}
          <div className="form-section">
            <div className="section-header">
              <h2 className="section-title">
                ULD BLGLER
              </h2>
            </div>
            
            {/* Uak ve Airline Seimi */}
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="aircraft" className="form-label">
                  Uak Tipi
                  <span className="label-optional"> (Opsiyonel)</span>
                </label>
                <select
                  id="aircraft"
                  name="aircraft"
                  value={selectedAircraft}
                  onChange={(e) => setSelectedAircraft(e.target.value)}
                  className="form-input"
                  data-testid="aircraft-select"
                  style={{
                    color: '#111827',
                    WebkitTextFillColor: '#111827',
                    opacity: 1,
                    filter: 'none',
                    textIndent: 0,
                    visibility: 'visible',
                    display: 'block',
                    backgroundColor: 'white',
                    border: '1px solid #d1d5db',
                    borderRadius: '0.375rem',
                    padding: '0.5rem 0.75rem',
                    height: '42px',
                    lineHeight: '1.5',
                    fontSize: '1rem',
                  }}
                >
                  <option value="">Uak tipi sein</option>
                  <option value="B767">Boeing 767</option>
                  <option value="B777">Boeing 777</option>
                  <option value="B787">Boeing 787</option>
                  <option value="A330">Airbus A330</option>
                  <option value="A350">Airbus A350</option>
                </select>
                <div className="form-helper-text info">
                  Uak tipine gre ULD limitleri otomatik ayarlanr
                </div>
              </div>
              
              <div className="form-group">
                <label htmlFor="airline" className="form-label">
                  Havayolu
                  <span className="label-optional"> (Opsiyonel)</span>
                </label>
                <select
                  id="airline"
                  name="airline"
                  value={selectedAirline}
                  onChange={(e) => setSelectedAirline(e.target.value)}
                  className="form-input"
                  data-testid="airline-select"
                  style={{
                    color: '#111827',
                    WebkitTextFillColor: '#111827',
                    opacity: 1,
                    filter: 'none',
                    textIndent: 0,
                    visibility: 'visible',
                    display: 'block',
                    backgroundColor: 'white',
                    border: '1px solid #d1d5db',
                    borderRadius: '0.375rem',
                    padding: '0.5rem 0.75rem',
                    height: '42px',
                    lineHeight: '1.5',
                    fontSize: '1rem',
                  }}
                >
                  <option value="">Havayolu sein</option>
                  <option value="TK">Turkish Airlines</option>
                  <option value="LH">Lufthansa</option>
                  <option value="AF">Air France</option>
                  <option value="KL">KLM</option>
                  <option value="BA">British Airways</option>
                </select>
                <div className="form-helper-text info">
                  Havayolu zel kstlamalar uygulanr
                </div>
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label htmlFor="uldCode" className="form-label">
                  ULD Kodu
                  <span className="label-required">*</span>
                </label>
                <input
                  type="text"
                  id="uldCode"
                  name="uldCode"
                  className={`form-input ${mobileClass} ${errors.uldCode ? 'error' : ''}`}
                  value={formData.uldCode}
                  onChange={handleInputChange}
                  data-testid="uld-code-input"
                  required
                  autoComplete="off"
                />
                {errors.uldCode && (
                  <div className="error-message">
                    {errors.uldCode}
                  </div>
                )}
                <div className={`form-helper-text ${getUldCodeHelper().type}`}>
                  {getUldCodeHelper().text}
                </div>
              </div>
              
              <div className="form-group">
                <label htmlFor="uldType" className="form-label">
                  ULD Tipi
                  <span className="label-required">*</span>
                </label>
                <div className="input-with-status">
                  <select
                    id="uldType"
                    name="uldType"
                    value={formData.uldType || ''}
                    onChange={handleUldTypeChange}
                    className={`form-input ${mobileClass} ${errors.uldType ? 'error' : ''}`}
                    data-testid="uld-type-select"
                    required
                    autoComplete="off"
                    size="1"
                    dir="ltr"
                    style={{
                      color: '#111827',
                      WebkitTextFillColor: '#111827',
                      opacity: 1,
                      filter: 'none',
                      textIndent: 0,
                      visibility: 'visible',
                      display: 'block',
                      backgroundColor: 'white',
                      border: '1px solid #d1d5db',
                      borderRadius: '0.375rem',
                      padding: '0.5rem 0.75rem',
                      height: '42px',
                      lineHeight: '1.5',
                      fontSize: '1rem',
                    }}
                  >
                    <option value="">ULD tipi sein</option>
                    {uldTypes.map(uld => (
                      <option key={uld.value} value={uld.value}>
                        {uld.label}
                      </option>
                    ))}
                  </select>
                </div>
                {errors.uldType && (
                  <div className="error-message">
                    {errors.uldType}
                  </div>
                )}
                <div className={`form-helper-text ${getUldTypeHelper().type}`}>
                  {getUldTypeHelper().text}
                </div>
              </div>
            </div>
          </div>

          {/* Arlk Bilgileri */}
          <div className={`form-section ${mobileClass}`}>
            <div className={`section-header ${mobileClass}`}>
              <h3 className={`section-title ${mobileClass}`}>
                AIRLIK BLGLER
              </h3>
            </div>
            
            <div className="form-row">
                             <div className="form-group">
                 <label htmlFor="maxWeight" className="form-label">
                   Maksimum Arlk (kg) = MGW
                 </label>
                   <div className="input-with-unit">
                     <input
                       type="number"
                       id="maxWeight"
                       name="maxWeight"
                       value={formData.maxWeight || ''}
                       className="form-input"
                       data-testid="max-weight-input"
                       readOnly
                       disabled
                       autoComplete="off"
                       min="0"
                       max="999999"
                       step="0.1"
                       title="ULD tipine gre otomatik doldurulur"
                     />
                     <span className="input-unit">kg</span>
                   </div>
                                   <div className={`current-weight-helper ${getMaxWeightHelper().type}`}>
                    <span className="helper-icon"></span>
                    <span className="helper-text">{getMaxWeightHelper().text}</span>
                  </div>
               </div>
              
                                            <div className="form-group">
                 <label htmlFor="tare" className="form-label">
                   Tare (kg)
                 </label>
                 <div className="tare-edit-container">
                   <div className="input-with-unit">
                     <input
                       type="number"
                       id="tare"
                       name="tare"
                       value={formData.tare || ''}
                       className={`form-input ${mobileClass} ${errors.tare ? 'error' : ''} ${getTareHelper().type === 'warning' ? 'error' : ''}`}
                       data-testid="tare-input"
                       onChange={handleInputChange}
                       autoComplete="off"
                       min="0"
                       max="999999"
                       step="0.1"
                       title={isTareEditable 
                         ? "Manuel dzenleme modu aktif. Deiiklikleri kaydetmek iin  ikonuna tklayn" 
                         : "ULD tipine gre otomatik. htiya olursa dzenleyin"}
                       readOnly={!isTareEditable}
                       disabled={!isTareEditable}
                     />
                     <span className="input-unit">kg</span>
                   </div>
                   
                   {/* Edit Mode Icons */}
                   {isTareEditable ? (
                     <div className="tare-edit-mode-icons">
                       <span 
                         className="tare-edit-mode-icon save"
                         onClick={saveTareChanges}
                         title="Deiiklikleri kaydet"
                         data-testid="tare-save-icon"
                       >
                         
                       </span>
                       <span 
                         className="tare-edit-mode-icon cancel"
                         onClick={cancelTareChanges}
                         title="Deiiklikleri iptal et"
                         data-testid="tare-cancel-icon"
                       >
                         
                       </span>
                     </div>
                   ) : (
                     <span 
                       className="tare-edit-icon"
                       onClick={toggleTareEdit}
                       title="ULD tipine gre otomatik. htiya olursa dzenleyin"
                       data-testid="tare-edit-icon"
                     >
                       
                     </span>
                   )}
                 </div>
                 {errors.tare && (
                   <div className="error-message">
                     {errors.tare}
                   </div>
                 )}
                                   <div className={`current-weight-helper ${getTareHelper().type}`}>
                    <span className="helper-icon"></span>
                    <span className="helper-text">{getTareHelper().text}</span>
                  </div>
               </div>
            </div>
            
            <div className="form-row">
                             <div className="form-group">
                 <label htmlFor="allowable" className="form-label">
                   Yklenebilir Arlk (kg)
                 </label>
                   <div className="input-with-unit">
                     <input
                       type="number"
                       id="allowable"
                       name="allowable"
                       value={formData.allowable || ''}
                       className="form-input"
                       data-testid="allowable-input"
                       readOnly
                       disabled
                       autoComplete="off"
                       min="0"
                       max="999999"
                       step="0.1"
                       title="MGW - Tare forml ile otomatik hesaplanr"
                     />
                     <span className="input-unit">kg</span>
                   </div>
                                                                      <div className={`current-weight-helper ${getAllowableHelper().type}`}>
                    <span className="helper-icon"></span>
                    <span className="helper-text">{getAllowableHelper().text}</span>
                  </div>
               </div>
              
              <div className="form-group">
                 <label htmlFor="currentWeight" className="form-label">
                   Mevcut Arlk (kg)
                   <span className="label-required">*</span>
                 </label>
                 <input
                     type="number"
                     id="currentWeight"
                     name="currentWeight"
                     value={formData.currentWeight}
                     className={`form-input ${mobileClass} ${errors.currentWeight ? 'error' : ''} ${getCurrentWeightHelper().type === 'error' ? 'error' : ''}`}
                     onChange={handleInputChange}
                     data-testid="current-weight-input"
                     required
                     autoComplete="off"
                     placeholder="Arlk giriniz (kg)"
                     title="0 - Yklenebilir Arlk aralnda olmaldr"
                     disabled={!formData.uldType}
                     aria-disabled={!formData.uldType}
                     tabIndex={!formData.uldType ? -1 : 0}
                     min="0"
                     max="999999"
                     step="1"
                   />
                   {/* Error mesaj kaldrld - helper metni yeterli */}
                 
                   {/* Tek Satr Helper - Sabit Ykseklik */}
                   <div 
                     className={`current-weight-helper ${getCurrentWeightHelper().type}`} 
                     aria-live="polite"
                     role={getCurrentWeightHelper().type === 'error' ? 'alert' : undefined}
                   >
                     <span className="helper-icon">
                       {getCurrentWeightHelper().type === 'disabled' && ''}
                       {getCurrentWeightHelper().type === 'normal' && ''}
                       {getCurrentWeightHelper().type === 'warning' && ''}
                       {getCurrentWeightHelper().type === 'critical' && ''}
                       {getCurrentWeightHelper().type === 'error' && ''}
                     </span>
                     <span className="helper-text">
                       {getCurrentWeightHelper().text}
                     </span>
                   </div>
                </div>
            </div>
          </div>

          {/* Kapasite ve Hacim Bilgileri */}
          <div className={`form-section ${mobileClass}`}>
            <div className={`section-header ${mobileClass}`}>
              <h3 className={`section-title ${mobileClass}`}>
                KAPASTE VE HACM
              </h3>
            </div>
            
            <div className="form-row">
                             <div className="form-group">
                                   <label htmlFor="volume" className="form-label">
                    Hacim (m)
                    {formData.uldType && uldTypes.find(uld => uld.value === formData.uldType)?.type === 'pallet' && (
                      <span className="label-optional"> (Opsiyonel)</span>
                    )}
                  </label>
                  <input
                    type="number"
                    id="volume"
                    name="volume"
                    value={formData.volume}
                    className="form-input"
                    data-testid="volume-input"
                    readOnly={!formData.uldType || uldTypes.find(uld => uld.value === formData.uldType)?.type !== 'pallet'}
                    disabled={!formData.uldType || uldTypes.find(uld => uld.value === formData.uldType)?.type !== 'pallet'}
                    onChange={handleInputChange}
                    autoComplete="off"
                    step="0.1"
                    min="0"
                    max="999"
                    placeholder={formData.uldType && uldTypes.find(uld => uld.value === formData.uldType)?.type === 'pallet' ? "Yk konturne gre" : ""}
                                         title={formData.uldType && uldTypes.find(uld => uld.value === formData.uldType)?.type === 'pallet' 
                       ? "Palet iin opsiyonel - yk konturne gre" 
                       : "ULD tipine gre otomatik"}
                  />
                 <div className={`form-helper-text ${getVolumeHelper().type}`}>
                   {getVolumeHelper().text}
                 </div>
               </div>
              
                                                           {/* Kapasite Kullanm (%) Input - Kaldrld */}
            </div>

                         {/* Kapasite Bar */}
             {formData.capacityUsage && (
               <div className="capacity-bar-container">
                 <div className="capacity-bar">
                   <div 
                     className="capacity-bar-fill"
                     style={{ width: `${getCapacityBarPercentage()}%`, backgroundColor: getCapacityBarColor() }}
                   ></div>
                 </div>
                 <div className="capacity-bar-label">
                   Kapasite: {formatPercent(formData.capacityUsage)}%
                   {getOverflowInfo() && (
                     <span className="overflow-badge">
                        Ald: +{formatKg(getOverflowInfo().kg)} kg
                     </span>
                   )}
                 </div>
                 
                                   {/* Progress Bar Uyar Metni - Kaldrld */}
               </div>
             )}

                         {/* Kapasite Uyar Banner - Kaldrld */}

            <div className="form-row">
                             <div className="form-group">
                 <label htmlFor="remainingWeight" className="form-label">
                   Kalan Arlk (kg)
                 </label>
                 <input
                   type="text"
                   id="remainingWeight"
                   name="remainingWeight"
                   value={(() => {
                     const remaining = parseFloat(formData.remainingWeight) || 0;
                     if (remaining < 0) {
                       return `Kapasite ${formatKg(Math.abs(remaining))} kg ald`;
                     }
                     return formData.remainingWeight ? `${formatKg(formData.remainingWeight)} kg` : '';
                   })()}
                   className={`form-input ${mobileClass} ${parseFloat(formData.remainingWeight) < 0 ? 'error' : ''}`}
                   data-testid="remaining-weight-input"
                   readOnly
                   disabled
                   autoComplete="off"
                   title="Yklenebilir Arlk - Mevcut Arlk"
                 />
                 <div className={`form-helper-text ${getRemainingWeightHelper().type}`}>
                   {getRemainingWeightHelper().text}
                 </div>
               </div>
            </div>
          </div>

          {/* Form Actions */}
          <div className="form-actions">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={handleClearForm}
              disabled={loading}
              data-testid="clear-uld-form-button"
            >
              Formu Temizle
            </button>
            
            <button
              type="submit"
              className={`btn ${isSubmitDisabled() ? 'btn-disabled' : 'btn-primary'}`}
              disabled={isSubmitDisabled()}
              data-testid="submit-uld-form-button"
              title={getSubmitButtonTooltip()}
            >
              {loading ? (
                <>
                  <span className="loading-spinner"></span>
                  Kaydediliyor...
                </>
              ) : (
                'ULD Kaydet'
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  )
}

export default UldForm