import { create } from 'zustand'
import { persist } from 'zustand/middleware'

// TEK KAYNAKLI DURUM MODEL
const useScanSimulatorStore = create(
  persist(
    (set, get) => ({
      //  4 Ana State Deikeni (Tek Kaynak)
      offline: false,       // true/false
      Q: 0,                // kuyruk: tamsay
      P: 0,                // ileniyor: tamsay  
      T: 0,                // toplam: tamsay (son 24 saatte baarl)
      
      // Yardmc state (UI iin)
      queue: [],
      scannedBarcodes: [],
      
      //  1) Toggle: Offline Modu
      setOfflineMode: (mode) => {
        console.log(` STORE setOfflineMode: ${get().offline}  ${mode}`)
        set({ offline: mode })
        // Toggle hibir sayac dorudan deitirmez
        console.log(` STORE updated: offline=${get().offline}, Q=${get().Q}, P=${get().P}, T=${get().T}`)
      },
      
      addToQueue: (barcode) => {
        const newItem = {
          id: Date.now(),
          barcode,
          ts: new Date().toISOString(),
          status: 'pending'
        }
        set(state => ({
          queue: [...state.queue, newItem]
        }))
      },
      
      processQueue: async () => {
        const { queue } = get()
        if (queue.length === 0) return
        
        const queueLength = queue.length
        const itemsToProcess = [...queue] // Kuyruu kopyala
        
        // leniyor sayacn balat, kuyruu temizle
        set({ 
          processingCount: queueLength,
          queue: [] // Kuyruu hemen temizle
        })
        
        // Simlasyon iin gecikme ekle (2 saniye)
        await new Promise(resolve => setTimeout(resolve, 2000))
        
        // lenen eleri scannedBarcodes'a ekle
        const processedItems = itemsToProcess.map(item => ({
          ...item,
          status: 'success',
          processedAt: new Date().toISOString()
        }))
        
        // Atomik gncelleme: processingCount = 0, processedCount += ilenen say
        set(state => ({
          scannedBarcodes: [...state.scannedBarcodes, ...processedItems],
          processingCount: 0,
          processedCount: state.processedCount + queueLength
        }))
      },
      
      clearQueue: () => set({ queue: [] }),
      
      addScannedBarcode: (barcode) => {
        const newItem = {
          id: Date.now(),
          barcode,
          ts: new Date().toISOString(),
          status: 'scanned'
        }
        set(state => ({
          scannedBarcodes: [...state.scannedBarcodes, newItem]
        }))
      },

      // Online modda annda ilem
      processOnline: (barcode) => {
        const newItem = {
          id: Date.now(),
          barcode,
          ts: new Date().toISOString(),
          status: 'success',
          processedAt: new Date().toISOString()
        }
        // Atomik gncelleme: processedCount +1
        set(state => ({
          scannedBarcodes: [...state.scannedBarcodes, newItem],
          processedCount: state.processedCount + 1
        }))
      },
      
      clearScannedBarcodes: () => set({ 
        scannedBarcodes: [],
        processedCount: 0
      }),
      
      // Basit getters
      getQueueLength: () => get().queue.length,
      getScannedCount: () => get().scannedBarcodes.length,
      getProcessingCount: () => get().processingCount,
      getProcessedCount: () => get().processedCount,
      getTotalProcessed: () => get().processedCount
    }),
    {
      name: 'scan-simulator-store',
      partialize: (state) => ({
        offlineMode: state.offlineMode,
        queue: state.queue,
        scannedBarcodes: state.scannedBarcodes,
        processedCount: state.processedCount
      })
    }
  )
)

export default useScanSimulatorStore
