import React, { useState, useEffect } from 'react'
import { useNotification } from '../../contexts/NotificationContext'
import { useOffline } from '../../contexts/OfflineContext'
import './MobileBarcode.css'

const MobileBarcode = () => {
  //  TEK DURUM MODEL - 4 Ana State (Spesifikasyona gre)
  const [offline, setOffline] = useState(false)  // true/false
  const [Q, setQ] = useState(0)                  // kuyruk: int
  const [P, setP] = useState(0)                  // ileniyor: int
  const [T, setT] = useState(0)                  // toplam: int (24 saat baarl)

  // Yardmc state (UI iin)
  const [queueItems, setQueueItems] = useState([])
  const [showClearModal, setShowClearModal] = useState(false)


  const [manualBarcode, setManualBarcode] = useState('')
  const [manualError, setManualError] = useState('')
  const [expandedStep, setExpandedStep] = useState(null)
  
  // Debounce koruma - ift tklama nleme
  const [lastActionTime, setLastActionTime] = useState(0)
  const DEBOUNCE_DELAY = 300 // 300ms
  
  // Debounce check fonksiyonu
  const checkDebounce = () => {
    const now = Date.now()
    if (now - lastActionTime < DEBOUNCE_DELAY) {
      return false
    }
    setLastActionTime(now)
    return true
  }
  
  // Notification context
  const { showSuccess, showError, showInfo } = useNotification()


  // Barkod dorulama fonksiyonu
  const validateBarcode = (barcode) => {
    const cleanBarcode = (barcode || '').trim().toUpperCase()
    const uldRegex = /^ULD-[A-Z]{3}\d{5}[A-Z]{2}$/
    const awbRegex = /^\d{3}-\d{8}$/
    const testRegex = /^TEST-\d{5}$/
    const uldTestRegex = /^ULD-TEST\d+$/
    
    return uldRegex.test(cleanBarcode) || awbRegex.test(cleanBarcode) || 
           testRegex.test(cleanBarcode) || uldTestRegex.test(cleanBarcode)
  }

  // A) Toggle (Offline Modu) - SPESFKASYON
  const handleOfflineModeToggle = () => {
    if (!checkDebounce()) {
      return
    }

    const newOfflineMode = !offline

    // 1. State'i gncelle (sadece offline, sayalara dokunma)
    setOffline(newOfflineMode)

    // 2. UI gncelleme (buton aktifliklerini yenile)
    // 3. Toast gster - detayl mesajlar
    if (newOfflineMode) {
      showSuccess('Offline moda geildi  barkodlar kuyrua eklenecek.')
    } else {
      showSuccess('Online moda geildi  barkodlar direkt ilenecek.')
    }
  }

  // B) Barkod Tara / Oku - SPESFKASYON
  const handleBarcodeScan = async (barcode) => {
    if (!checkDebounce()) return

    console.log(` B) Barkod Tara: offline=${offline}, barcode=${barcode}`)

    // Barkod dorula (format)
    const isValid = validateBarcode(barcode)

    if (offline) {
      // offline=true (yeil): Geerliyse Q = Q + 1. (P ve T deimez)
      if (isValid) {
        const newQ = Q + 1
        setQ(newQ)

        // Kuyrua ekle (UI iin)
        const newItem = {
          id: Date.now(),
          barcode,
          timestamp: new Date().toISOString(),
          status: 'pending'
        }
        setQueueItems(prev => [...prev, newItem])

        showSuccess('Barkod kuyrua eklendi.')
      } else {
        showError('Format hatal  ilenmedi.')
      }
    } else {
      // offline=false (gri): P = 1  ilem  sonu
      setP(1)
      console.log(` Online: P=0  1 (ilem balad)`)
      
      try {
        // Simlasyon - ileme sresi
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        if (isValid) {
          // Baarl  T = T + 1
          const newT = T + 1
          setT(newT)
          setP(0)
          showSuccess('Barkod ilendi.')
        } else {
          // Hatal  T deimez
          setP(0)
          showError('Format hatal  ilenmedi.')
        }
      } catch (error) {
        setP(0)
        showError('Format hatal  ilenmedi.')
      }
    }
  }

  // C) Kuyruu le - SPESFKASYON (sadece offline=false ve Q>0)
  const handleProcessQueue = async () => {
    if (!checkDebounce()) return

    console.log(` C) Kuyruu le: offline=${offline}, Q=${Q}`)

    // Guard: offline=true iken arlmamal
    if (offline) {
      console.log(' Guard: offline=true, ilem blokland')
      // Offline modda pasif, popup yok (spesifikasyona gre)
      return
    }
    
    if (Q === 0) {
      showInfo('Kuyruk bo.')
      return
    }
    
    console.log(` Kuyruu le balad: Q=${Q}`)
    
    // Yeni mantk: Balang leniyor = Kuyruk, Kuyruk=0
    const totalItemsToProcess = Q
    setP(totalItemsToProcess) // leniyor = Kuyruk
    setQ(0) // Kuyruk = 0 (hemen)

    try {
      let processedCount = 0
      let currentP = totalItemsToProcess
      let currentT = T

      // Her barkodu tek tek ile
      for (let i = 0; i < totalItemsToProcess; i++) {
        // Simlasyon - her item iin ilem sresi
        await new Promise(resolve => setTimeout(resolve, 500))

        // %85 baar oran
        const isSuccess = Math.random() < 0.85

        if (isSuccess) {
          // Baarl  Toplam++
          currentT++
          setT(currentT)
        }

        // Her durumda leniyor--
        currentP--
        setP(currentP)
        processedCount++

        console.log(` lem ${processedCount}/${totalItemsToProcess}: P=${currentP}, T=${currentT}`)
      }

      // Final: leniyor=0
      setP(0)

      // Kuyruk listesini temizle
      setQueueItems([])

      showSuccess(`Kuyruktaki ${totalItemsToProcess} barkod ilendi.`)
      
    } catch (error) {
      setP(0)
      showError('Kuyruk ileme hatas.')
    }
  }

  // D) Kuyruu Temizle - SPESFKASYON
  const handleClearQueue = () => {
    console.log(` D) Kuyruu Temizle: Q=${Q}, P=${P}`)

    if (Q === 0) {
      showInfo(' Kuyruk zaten bo')
      return
    }
    
    // Onay modal gster
    setShowClearModal(true)
  }

  const confirmClearQueue = () => {
    console.log(` D) Onayland: Q=${Q}, P=${P}`)

    // Kuyruk=0, leniyor=0, Toplam deimez (spesifikasyona gre)
    setQ(0)
    setP(0)
    setQueueItems([])
    
    showSuccess('Kuyruk baaryla temizlendi.')

    setShowClearModal(false)
  }

  return (
    <div className="mobile-page">
      {/* Professional Form Header */}
      <div className="form-header">
        <h1 className="form-title" data-testid="mobile-barcode-title">Barkod Simlasyonu</h1>
        <p className="form-subtitle">Test barkodlar retin ve sonular grn</p>
        </div>

      {/* Toggle Container - Basit */}
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          padding: '20px',
          background: 'rgba(255, 255, 255, 0.95)',
          margin: '20px 20px 24px 20px',
          borderRadius: '12px',
          boxShadow: '0 2px 8px rgba(0, 0, 0, 0.06)'
        }}>
          <span style={{
            fontSize: '16px',
            fontWeight: '600',
            color: '#374151'
          }}>Offline Modu</span>
          
          <div 
              onClick={handleOfflineModeToggle}
            style={{
              position: 'relative',
              width: '52px',
              height: '28px',
              borderRadius: '14px',
              backgroundColor: offline ? '#28a745' : '#cccccc',
              cursor: 'pointer',
              transition: 'background-color 0.3s ease'
            }}
          >
            <div style={{
              position: 'absolute',
              top: '2px',
              left: offline ? '26px' : '2px',
              width: '24px',
              height: '24px',
              background: 'white',
              borderRadius: '50%',
              transition: 'left 0.3s ease',
              boxShadow: '0 2px 6px rgba(0, 0, 0, 0.3)'
            }}></div>
          </div>
        </div>
      
      {/* Kontrol Paneli - 3 Kutucuk */}
      <div className="mobile-control-stats">
        <div className="mobile-stat-box">
          <div className="mobile-stat-number">{Q}</div>
          <div className="mobile-stat-title">Kuyruk</div>
        </div>
        <div className="mobile-stat-box">
          <div className="mobile-stat-number">{P}</div>
          <div className="mobile-stat-title">leniyor</div>
        </div>
        <div className="mobile-stat-box">
          <div className="mobile-stat-number">{T}</div>
          <div className="mobile-stat-title">Toplam</div>
        </div>
      </div>

      {/* Aksiyon Butonlar - Etkinlik Matrisi Uyguland */}
      <div className="mobile-action-section">
        {/* Barkod Tara: Her zaman aktif */}
        <button
          className="mobile-action-btn mobile-action-primary"
          onClick={() => handleBarcodeScan('ULD-TEST12345')} // Test barkodu
          title="Kamera ile barkod okur veya rastgele barkod retir"
        >
          Barkod Tara / Oku
        </button>
        
        {/* Kuyruu le: Yalnz Online + Q>0 iken aktif */}
        <button
          className={`mobile-action-btn mobile-action-secondary ${
            offline || Q === 0 || P > 0 ? 'disabled' : ''
          }`}
          onClick={handleProcessQueue}
          disabled={offline || Q === 0 || P > 0}
          title={
            offline
              ? 'Offline modda pasif - Online moda gein' 
              : Q === 0
                ? 'Kuyruk bo - nce barkod ekleyin'
                : P > 0
                  ? 'lem devam ediyor'
                : 'Kuyruktaki barkodlar toplu ile'
          }
        >
          {P > 0 ? 'leniyor' : 'Kuyruu le'}
        </button>
        
        {/* Kuyruu Temizle: Q>0 iken aktif (mod fark etmez) */}
        <button
          className={`mobile-action-btn mobile-action-danger ${
            Q === 0 ? 'disabled' : ''
          }`}
          onClick={handleClearQueue}
          disabled={Q === 0}
          title={
            Q === 0
              ? 'Kuyruk bo - temizlenecek barkod yok'
              : `${Q} barkod var - kuyruu temizle`
          }
        >
          Kuyruu Temizle
        </button>
      </div>
          
      {/* Test Barkodlar Listesi */}
      <div className="mobile-test-section">
        <h3>Test Barkodlar</h3>
        <div className="mobile-test-list">
          {[
            { code: 'ULD-AKE12345AB', type: 'ULD', valid: true },
            { code: '123-45678901', type: 'AWB', valid: true },
            { code: 'INVALID-123', type: 'ERR', valid: false },
            { code: 'BAD-FORMAT', type: 'ERR', valid: false }
          ].map((item, index) => (
            <div
              key={index}
              className={`mobile-test-item ${item.valid ? 'valid' : 'invalid'}`}
              onClick={() => handleBarcodeScan(item.code)}
            >
              <div className="mobile-test-left">
                <span className={`mobile-test-type ${item.valid ? 'valid' : 'invalid'}`}>
                  {item.type}
              </span>
                <span className="mobile-test-code">{item.code}</span>
              </div>
              <div className="mobile-test-status">
                {item.valid ? '' : ''}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Manuel Giri */}
      <div className="mobile-manual-section">
        <h3>Manuel Giri</h3>
        <div className="mobile-manual-input-group">
            <input
              type="text"
              value={manualBarcode}
            onChange={(e) => setManualBarcode(e.target.value.toUpperCase())}
              placeholder="Barkod giriniz (rn: ULD-AKE12345AB)"
            className="mobile-manual-barcode-input"
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault()
                if (manualBarcode.trim()) {
                  handleBarcodeScan(manualBarcode.trim())
                  setManualBarcode('')
                  setManualError('')
                }
              }
            }}
          />
          {manualError && (
            <div className="mobile-manual-error">{manualError}</div>
          )}
            <button
            className="mobile-manual-test-btn btn btn-primary"
            onClick={() => {
              if (manualBarcode.trim()) {
                handleBarcodeScan(manualBarcode.trim())
                setManualBarcode('')
                setManualError('')
              } else {
                setManualError('Barkod giriniz')
              }
            }}
              disabled={!manualBarcode.trim()}
            >
              Test Et
            </button>
        </div>
      </div>

      {/* Senaryo Admlar */}
      <div className="mobile-scenario-section">
        <h3>Senaryo Admlar</h3>
        <div className="mobile-scenario-accordion">
          
                <div className="mobile-accordion-item">
                  <button 
                    className="mobile-accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 1 ? null : 1)}
                    aria-expanded={expandedStep === 1}
              aria-controls="step-1-content"
                  >
                    <div className="mobile-step-badge">1</div>
              <div className="mobile-step-title">Offline modu aktif edin</div>
                    <div className="mobile-accordion-chevron">
                      {expandedStep === 1 ? '' : ''}
            </div>
                  </button>
                  {expandedStep === 1 && (
              <div id="step-1-content" className="mobile-accordion-content">
                <p>Toggle ile offline modu an. Bu modda barkodlar kuyrua eklenir.</p>
        </div>
                  )}
      </div>

                <div className="mobile-accordion-item">
                  <button 
                    className="mobile-accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 2 ? null : 2)}
                    aria-expanded={expandedStep === 2}
              aria-controls="step-2-content"
                  >
                    <div className="mobile-step-badge">2</div>
              <div className="mobile-step-title">Bir barkod sein veya girin</div>
                    <div className="mobile-accordion-chevron">
                      {expandedStep === 2 ? '' : ''}
              </div>
                  </button>
                  {expandedStep === 2 && (
              <div id="step-2-content" className="mobile-accordion-content">
                <p>Test barkodlarndan birini tklayn veya manuel giri yapn.</p>
                </div>
              )}
            </div>

                <div className="mobile-accordion-item">
                  <button 
                    className="mobile-accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 3 ? null : 3)}
                    aria-expanded={expandedStep === 3}
              aria-controls="step-3-content"
                  >
                    <div className="mobile-step-badge">3</div>
              <div className="mobile-step-title">Barkod kuyrua eklenir</div>
                    <div className="mobile-accordion-chevron">
                      {expandedStep === 3 ? '' : ''}
        </div>
                  </button>
                  {expandedStep === 3 && (
              <div id="step-3-content" className="mobile-accordion-content">
                <p>Kuyruk sayac artar ve barkod beklemeye alnr.</p>
                    </div>
                  )}
      </div>

                <div className="mobile-accordion-item">
        <button
                    className="mobile-accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 4 ? null : 4)}
                    aria-expanded={expandedStep === 4}
              aria-controls="step-4-content"
                  >
                    <div className="mobile-step-badge">4</div>
              <div className="mobile-step-title">"Kuyruu le" butonuna tklayn</div>
                    <div className="mobile-accordion-chevron">
                      {expandedStep === 4 ? '' : ''}
                    </div>
        </button>
                  {expandedStep === 4 && (
              <div id="step-4-content" className="mobile-accordion-content">
                <p>nce online moda gein. Kuyruktaki barkodlar ileyin. leniyor sayac geici olarak artar.</p>
                    </div>
                  )}
                </div>

                <div className="mobile-accordion-item">
                  <button 
                    className="mobile-accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 5 ? null : 5)}
                    aria-expanded={expandedStep === 5}
              aria-controls="step-5-content"
                  >
                    <div className="mobile-step-badge">5</div>
              <div className="mobile-step-title">Kuyruk ilenir ve temizlenir</div>
                    <div className="mobile-accordion-chevron">
                      {expandedStep === 5 ? '' : ''}
                    </div>
                  </button>
                  {expandedStep === 5 && (
              <div id="step-5-content" className="mobile-accordion-content">
                <p>Toplam sayac gncellenir ve kuyruk temizlenir.</p>
                    </div>
                  )}
            </div>

          </div>
      </div>

      {/* Kuyruu Temizle Modal - Mobil Optimize */}
      {showClearModal && (
        <div className="mobile-modal-overlay" onClick={() => setShowClearModal(false)}>
          <div className="mobile-modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="mobile-modal-header">
              <h3 className="mobile-modal-title">Kuyruu Temizle</h3>
            </div>
            
            <div className="mobile-modal-body">
              <p className="mobile-modal-text">
                Kuyrukta {Q} kayt var. Bu ilem geri alnamaz. Devam etmek istiyor musunuz?
              </p>
              </div>
              
            <div className="mobile-modal-buttons">
                <button
                className="mobile-modal-btn mobile-modal-btn-secondary"
                onClick={() => setShowClearModal(false)}
              >
                Vazge
                </button>
                <button
                className="mobile-modal-btn mobile-modal-btn-danger"
                onClick={confirmClearQueue}
              >
                Evet, Temizle
                </button>
              </div>
          </div>
        </div>
      )}
    </div>
  )
}

export default MobileBarcode